<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process130"></a>

<p><br></p>
<p><span class="rvts78">暴君套装（</span><span class="rvts79">Artifacts of The Tyrant</span><span class="rvts78">）</span><span class="rvts80">.</span><span class="rvts78">下篇</span><span class="rvts79">[</span><span class="rvts78">支线</span><span class="rvts79">]</span></p>
<p><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:435,Y:281</span><span class="rvts21">，暴君战靴</span>
<br><span class="rvts20">X:589,Y:678</span><span class="rvts21">，暴君之手</span>
<br><span class="rvts20">X:700,Y:502</span><span class="rvts21">，暴君吊坠</span>
<br><span class="rvts20">X:319,Y:499</span><span class="rvts21">，暴君头盔</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找到剩余的暴君套装</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1049.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">暴君头盔位于废旧兵营的地下室，需要先完成</span><span class="rvts20"></span><span class="rvts21">军械库</span><span class="rvts20"></span><span class="rvts21">支线任务。如果在尝试打开该武器匣失败后，可以和军械库中的那名被诅咒的士兵谈话询问详情，便可打开。</span>
<br><span class="rvts52"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1050.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">暴君吊坠位于黑暗峡谷的藏宝室，需要先完成</span><span class="rvts20"></span><span class="rvts21">布拉克斯</span><span class="rvts20"></span><span class="rvts21">雷克斯藏宝室</span><span class="rvts20"></span><span class="rvts21">。拿下该处的灵柩罐，便可以获得。</span>
<br><span class="rvts52"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1051.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">暴君战靴位于海滩边的那处高塔废墟中，由于高塔废墟内部是充能的水气，因此一旦从对面岩石处传送过去，就会被震击掉下塔。</span>
<br><span class="rvts21">正确的方式是，需要首先祝福塔内的充能水气，这样以来，地表变为了</span><span class="rvts20"></span><span class="rvts21">被祝福的充能水气</span><span class="rvts20"></span><span class="rvts21">，就不会受到震击效果。</span>
<br><span class="rvts21">暴君战靴位于里面的一个尸体处。使用传送技能，很容易拿到。</span>
<br><span class="rvts52"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1052.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">最难找的暴君套装位于雷克斯密室，雷克斯密室在一处隐匿的塔里。然而想要进入该塔，需要首先走出一片迷宫。详情请见</span><span class="rvts20"></span><span class="rvts21">神龙迷宫</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts21">在第二部棺材内，存在着暴君之手，然而打开石棺需要</span><span class="rvts20">18</span><span class="rvts21">点的力量。或可以使用陷阱解除器进行拆除石棺的机关，拆除后，石棺会留下毒雾。（经测试，拆除机关只能在主机版有效，</span><span class="rvts20">PC</span><span class="rvts21">版无该功能）</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



